import Vue from 'vue';
import Router from 'vue-router';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Front',
      component: () => import('./views/Front.vue'),
      children: [
        {
          path: '/',
          name: 'Simple_Start',
          component: () => import('./views/pages/Simple_Start.vue'),
        },
        {
          path: '/play',
          name: 'Simple',
          component: () => import('./views/pages/Simple.vue'),
        },
        {
          path: '/end',
          name: 'Simple_End',
          component: () => import('./views/pages/Simple_End.vue'),
        },
        {
          path: '/message',
          name: 'Message',
          component: () => import('./views/pages/Message.vue'),
        },
        {
          path: '/personal',
          name: 'Personal',
          component: () => import('./views/pages/Personal.vue'),
          meta: { requiresAuth: true },
        },
        {
          path: '/ranking',
          name: 'Ranking',
          component: () => import('./views/pages/Ranking.vue'),
        },
      ],
    },
    {
      path: '/login',
      name: 'Login',
      component: () => import('./views/pages/Login.vue'),
    },
    {
      path: '/game',
      name: 'Game',
      component: () => import('./views/Game.vue'),
      children: [
        {
          path: 'end',
          name: 'Game_End',
          component: () => import('./views/pages/Game_End.vue'),
        },
        {
          path: 'start',
          name: 'Game_Start',
          component: () => import('./views/pages/Game_Start.vue'),
        },
        {
          path: 'play',
          name: 'Play',
          component: () => import('./views/pages/Play.vue'),
          meta: {
            keepAlive: true,
          },
        },
      ],
    },
  ],
});
